footer {
  background-color: $blue-ing;
  color: white;
  a {
    color: white;
    text-decoration: none;
    transition: all 0.3s;
    &:hover {
      color: $gray-ing;
      text-decoration: underline;
    }
  }
  .redes {
    ul {
      display: flex;
      list-style: none;
      padding: 0;
      li {
        a {
          i {
            color: white;
            transition: all 0.3s;
          }
          &:hover {
            i {
              color: $gray-ing;
              transform: scale(1.3);
            }
          }
        }
      }
    }
  }
  .nav-item {
    .nav-link {
      color: white;
      border-bottom: 2px solid transparent;
      text-align: center;
      &.active:before {
        left: 0;
        right: 0;
        background: white;
      }
    }
    &:hover {
      .nav-link {
        text-decoration: none;
      }
      .nav-link:before {
        background: white;
      }
    }
  }
}