#navbar {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 1030;
  transition: all 0.3s;
  .navbar {
    background-color: $gray-ing;
    transition: all 0.3s;
    @media (max-width: 1199.98px) {
      max-width: 100%;
    }
    a {
      font-weight: 400;
    }
    .navbar-brand {
      padding-top: 0px;
      padding-bottom: 0px;
      .logo-navbar {
        height: 100px;
        transition: all 0.5s;
      }
    }
    .navbar-toggler {
      border: none;
      outline: none;
      box-shadow: none;
    }
    .offcanvas {
      background-color: $gray-ing;
    }
    .navbar-nav {
      .nav-item {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        .nav-link {
          color: $blue-ing;
          font-size: 1rem;
          font-weight: 600;
          border-bottom: 2px solid transparent;
          text-align: start;
          width: 100%;
          &.active:before {
            left: 0;
            right: 0;
            background: $blue-ing;
          }
        }
        &:hover {
          .nav-link:before {
            background: $blue-ing;
          }
        }
        .dropdown-menu {
          background-color: $gray-ing;
          border: none;
          border-radius: 0.5rem;
          border-top-left-radius: 0;
          border-top-right-radius: 0;
          box-shadow: 5px 5px 25px -10px black;
          &[data-bs-popper] {
            margin-top: 0;
          }
          .dropdown-item {
            color: $blue-ing;
            font-size: 0.9rem;
            font-weight: 600;
            border-radius: 0.3rem;
            transition: all 0.3s;
            &:hover {
              background-color: rgba($blue-ing, $alpha: 1);
              color: $gray-ing;
            }
          }
        }
      }
    }
  }
}