// ------------------------------------------------
// HOME
// ------------------------------------------------

#home {
  #welcome {
    margin-top: 74px;
    width: 100%;
    height: 25vh;
    background-image: url(../images/home/welcome.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @media (min-width: 992px) {
      margin-top: 116px;
      height: 70vh;
    }
  }
}

// ------------------------------------------------
// WELCOME INTERNAS
// ------------------------------------------------

.interna {
  #welcome {
    margin-top: 74px;
    width: 100%;
    height: 25vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @media (min-width: 992px) {
      margin-top: 116px;
    }
    h1 {
      text-shadow: 0px 0px 30px white;
    }
  }
}
#nosotros {
  #welcome {
