html, body {
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
  color: black;
  font-size: 14px;
  @media (min-width: 425px) {
    font-size: 15px;
  }
  @media (min-width: 768px) {
    font-size: 16px;
  }
}

.btn-close {
  --bs-btn-close-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23003A79'%3e%3cpath d='M.293.293a1 1 0 0 1 1.414 0L8 6.586 14.293.293a1 1 0 1 1 1.414 1.414L9.414 8l6.293 6.293a1 1 0 0 1-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 0 1-1.414-1.414L6.586 8 .293 1.707a1 1 0 0 1 0-1.414z'/%3e%3c/svg%3e");
  opacity: 1;
}

.text-blue {
  color: $blue-ing;
}
.text-gray {
  color: $gray-ing;
}
.list-check {
  list-style: circle;
}

.content {
  .nav-pills {
    .nav-link {
      color: $blue-ing;
      font-weight: bold;
      &.active {
        color: white;
        background-color: $blue-ing;
      }
    }
  }
}